<template>
  <div id="project">
    <h3>Projects</h3>

    




    <div>
      <b-card-group deck>
        <b-card title="Title" img-src="https://picsum.photos/300/300/?image=41" img-alt="Image" img-top>
          <b-card-text>
            This is a wider card with supporting text below as a natural lead-in to additional content.
            This content is a little bit longer.
          </b-card-text>
          <template #footer>
            <small class="text-muted">Last updated 3 mins ago</small>
          </template>
        </b-card>

        <b-card title="Title" img-src="https://picsum.photos/300/300/?image=41" img-alt="Image" img-top>
          <b-card-text>
            This card has supporting text below as a natural lead-in to additional content.
          </b-card-text>
          <template #footer>
            <small class="text-muted">Last updated 3 mins ago</small>
          </template>
        </b-card>

        <b-card title="Title" img-src="https://picsum.photos/300/300/?image=41" img-alt="Image" img-top>
          <b-card-text>
            This is a wider card with supporting text below as a natural lead-in to additional content.
            This card has even longer content than the first to show that equal height action.
          </b-card-text>
          <template #footer>
            <small class="text-muted">Last updated 3 mins ago</small>
          </template>
        </b-card>
      </b-card-group>
    </div>

  </div>
</template>

<style>
  #project {
    margin-top: 2%;
  }
  .card-deck {
    display: table-cell;
    width: 350px;
    /* height: 20%; */
  }
  .card-deck>.card>.card-body {
    background-color: #363636;
  }
  h4.card-title {
    min-height: 85px;
    font-weight: 500 !important;
  }
  .card-proj {
    margin-bottom: 5%;
  }
</style>

<script>
  import db from '../assets/firebaseInit'
  import Vue from 'vue'
  // import { CardPlugin } from 'bootstrap-vue'
  // Vue.use(CardPlugin)
  import { ButtonPlugin } from 'bootstrap-vue'
  Vue.use(ButtonPlugin)

  export default {
    name: 'project',
    data () {
      return {
        project_id: null,
        project_academic_arr: [],
        project_nonacademic_arr: [],
        flag_employment: [],
        flag_project: [],
        loading: true,
        doc_id: 'RAVqGWKerLwJRVJyBVXf'
      }
    },
    created () {
      db.collection('personal_details').doc('RAVqGWKerLwJRVJyBVXf').collection('projects').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'project_id': doc.data().project_id,
            'project_name': doc.data().project_name,
            'description': doc.data().description,
            'employment': doc.data().employment,
            'flag': doc.data().flag
          }
          if (data.flag == 1) {
            this.project_nonacademic_arr.push(data)
            // console.log(this.project_nonacademic_arr)
          }
          else{
            this.project_academic_arr.push(data)
            // console.log(this.project_academic_arr)
          }
        })
      })
    },
    methods: {
      getImgUrl(pic) {
        return require('../images/projects/img'+pic+'.jpg')
      }
    }
  }
</script>


